import React, { useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import CssBaseline from '@mui/material/CssBaseline';
import Divider from '@mui/material/Divider';
import Drawer from '@mui/material/Drawer';
import IconButton from '@mui/material/IconButton';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import MenuIcon from '@mui/icons-material/Menu';
import CloseIcon from '@mui/icons-material/Close';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import { Theme } from '@mui/material/styles';
import { alpha } from '@mui/material/styles';
import { NavLink } from "react-router-dom";
import DarkModeIcon from '@mui/icons-material/DarkMode';
import LightModeIcon from '@mui/icons-material/LightMode';
import { useThemeContext } from '../../../context/themeContext';
import './Header.css';

interface Props {
  window?: () => Window;
}

const drawerWidth = '100%';
const navItems = [
  { label: 'home', path: '/' },
  { label: 'aboutMe', path: '/about-me' },
  { label: 'projects', path: '/projects' },
  { label: 'contacts', path: '/contacts' },
];


export default function DrawerAppBar(props: Props) {
  const { window } = props;
  const [mobileOpen, setMobileOpen] = React.useState(false);
  const { mode, toggleTheme } = useThemeContext();
  const { t, i18n } = useTranslation();

  // Встановлення мови при ініціалізації компонента
  useEffect(() => {
    const currentLanguage = localStorage.getItem('language') || 'en';
    i18n.changeLanguage(currentLanguage);
  }, [i18n]);


  // Функція для зміни мови
  const toggleLanguage = () => {
    const newLanguage = i18n.language === 'en' ? 'ua' : 'en';
    i18n.changeLanguage(newLanguage);
    localStorage.setItem('language', newLanguage);
  };

  const handleThemeChange = () => {
    toggleTheme(); // це автоматично перемикає тему між 'light' та 'dark'
    const newMode = mode === 'light' ? 'dark' : 'light'; // Отримуємо новий стан теми ПІСЛЯ виклику toggleTheme
    localStorage.setItem('themeMode', newMode);
  };
  

  const handleDrawerToggle = () => {
    setMobileOpen((prevState) => !prevState);
  };

  const drawer = (
    <Box 
      sx={{
        bgcolor: 'background.default', 
        height: '100%'
      }}
    >
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'space-between',
          px: 2
        }}
      >
        <Typography 
          variant="h6" 
          sx={{ my: 2 }}
          color='primary'
        >
          roma-delife
        </Typography>
        <IconButton
          color="primary"
          aria-label="open drawer"
          edge="end"
          onClick={handleDrawerToggle}
          sx={{ display: { sm: 'none' } }}
        >
          <CloseIcon />
        </IconButton>
      </Box>
      <Divider />
        <List
          sx={{
            py: 0
          }}
        >
          {navItems.map((item, index) => (
            <React.Fragment key={item.label}>
              <ListItem disablePadding>
                <ListItemButton sx={{ textAlign: 'center' }}>
                  <Button 
                    to={item.path} 
                    component={NavLink}
                    sx={{
                      textTransform: 'none',
                      fontWeight: 400,
                      fontSize: 16,
                      textDecoration: 'none', // Видалити підкреслення посилання
                      '&.active': { // Стилі для активного стану
                        bgcolor: (theme: Theme) => alpha(theme.palette.primary.main, 0.1),
                      }
                    }}
                  >
                    <ListItemText primary={t(`Header.${item.label}`)} />
                  </Button>
                </ListItemButton>
              </ListItem>
              {index !== navItems.length - 0 && <Divider />} 
            </React.Fragment>
          ))}
        </List>
    </Box>
  );

  const container = window !== undefined ? () => window().document.body : undefined;

  return (
    <Box sx={{ display: 'flex' }}>
      <CssBaseline />
      <AppBar 
        component="nav"
        className='Header'
        sx={{ 
          bgcolor: 'background.default',
          borderBottom: 1,
          borderColor: 'border', 
        }}
        enableColorOnDark
      >
        <Toolbar>
          <Box
            sx={{
              display: { xs: 'flex', sm: 'none' },
              width: '100%',  
              justifyContent: 'space-between'
            }}
          >
            <IconButton
              color="primary"
              aria-label="open drawer"
              edge="start"
              onClick={handleDrawerToggle}
              sx={{ mr: 2, display: { sm: 'none' } }}
            >
              <MenuIcon />
            </IconButton>
            <Typography
            variant="h6"
            component="span"
            sx={{ 
              flexGrow: 1,
              fontWeight: 400,
              lineHeight: '40px',
              fontSize: 16,
              display: { xs: 'block', sm: 'none' }, 
            }}
            color='primary'
          >
            roma-delife
          </Typography>
            <Box>
              <Button 
                onClick={toggleLanguage} 
                sx={{ marginLeft: 'auto' }}
              >
                {i18n.language === 'en' ? '_UA' : '_EN'}
              </Button>
              <IconButton 
                onClick={handleThemeChange} 
                edge="end"
              >
                {mode === 'dark' ? <DarkModeIcon color='primary'/> : <LightModeIcon color='primary'/>}
              </IconButton>
            </Box>
            
          </Box>
          <Typography
            variant="h6"
            component="span"
            sx={{ 
              flexGrow: 1,
              fontWeight: 400,
              fontSize: 16,
              display: { xs: 'none', sm: 'block' } 
            }}
            color='primary'
          >
            roma-delife
          </Typography>
          <Box 
            sx={{ 
              display: { xs: 'none', sm: 'flex' },
              gap: '5px' 
            }}>
            {navItems.map((item) => (
              <Button
                key={item.label}
                component={NavLink}
                to={item.path}
                color="primary"
                sx={{
                  textTransform: 'none',
                  fontWeight: 400,
                  fontSize: 16,
                  textDecoration: 'none', // Видалити підкреслення посилання
                  '&.active': { // Стилі для активного стану
                    borderBottom: '2px solid orange',
                    bgcolor: (theme) => alpha(theme.palette.primary.main, 0.1),
                  }
                }}
              >
                {t(`Header.${item.label}`)}
              </Button>
            ))}
            <Button 
              onClick={toggleLanguage} 
              sx={{ marginLeft: 'auto' }}
            >
                {i18n.language === 'en' ? '_UA' : '_EN'}
            </Button>
            <Button 
              onClick={toggleTheme} 
            >
              {mode === 'light' ? <DarkModeIcon color='primary'/> : <LightModeIcon color='primary'/>}
            </Button>
          </Box>
        </Toolbar>
      </AppBar>
      <nav>
        <Drawer
          container={container}
          variant="temporary"
          open={mobileOpen}
          onClose={handleDrawerToggle}
          ModalProps={{
            keepMounted: true, // Better open performance on mobile.
          }}
          sx={{
            display: { xs: 'block', sm: 'none' },
            '& .MuiDrawer-paper': { boxSizing: 'border-box', width: drawerWidth },
            bgcolor: 'background.default', 
          }}
        >
          {drawer}
        </Drawer>
      </nav>
    </Box>
  );
}